@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Yanone Kaffeesatz", sans-serif;
  color: rgb(13 9 237);
  text-shadow: 0px 0px 5px rgb(35, 105, 236);
  margin-top: 20px;
}

.post-component {
  background-color: rgb(237, 235, 235);
  padding: 20px;
  border: 2px solid black;
  border-radius: 20px;
  width: 60%;
}

.info-author {
  display: flex;
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.name-author {
  padding-left: 40px;
  display: flex;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

.name {
  font-size: 24px;
}

.nickname,
.date-p {
  color: rgb(10, 134, 242);
}

.context {
  padding-left: 140px;
}

.image {
  border-radius: 20px;
  width: 100%;
  height: 100%;
}

.icon {
  width: 35px;
  height: 35px;
}

.likes {
  display: flex;
  justify-content: space-around;
  padding-top: 20px;
}

.App {
  text-align: center;
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 32px;
  color: rgb(13 9 237);
  text-shadow: 0px 0px 5px rgb(35, 105, 236);
}

